// 	}
// 	componentWillUnmount() {

// 	}
// }


// Component가 너무 많거나 class를 만들기 힘들 때는 직접 컴포넌트에 스타일을 입힐 수 있다.
let Box = styled.div`
	padding: 20px;
`;

let Title = styled.h4`
	color : ${ props => props.색상 };
	font-size: 25px;
`;

const Detail = (props) => {

	const [stockAlert, setStockAlert] = useState(true);
	let [inputData, setInputData] = useState("");
	
	useEffect(() => {
		// 2초 후에 alert창 제거 
		let timer = setTimeout(() => {
			setStockAlert(false)
		}, 2000)
		console.log("실행");
		return () => { clearTimeout(timer)} // setTimeout시 버그 발생 가능성이 있음 -> clearTimeout을 통해 없애주기
		// return function 컴포넌트사라질때() { alert("호호") }
	}, []) // [] 이면 새로고침 혹은 페이지에 들어올 때만!! 실행됨


	let { id }= useParams();
	let history = useHistory();
	let 찾은상품 = props.shoes.find(function(상품){
    return 상품.id === Number(id)
  });

  return (
    <div className="container">
			<Box>
				{/* <Title 색상={'red'}>Detail</Title>
				<Title 색상='blue'>Detail</Title> */}
				<Title className="red">Detail</Title>
			</Box>

			{ inputData }

			<input onChange={(e) => { setInputData(e.target.value)}}/>
			{
				stockAlert
				? (<div className="my-alert2">
						<p>재고가 얼마 남지 않았습니다.</p>
					</div>)
				: null
			}
			
      <div className="row">
        <div className="col-md-6">
          <img alt="" src={`https://codingapple1.github.io/shop/shoes${Number(id)+1}.jpg`} width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{찾은상품.title}</h4>
          <p>{찾은상품.content}</p>
          <p>{찾은상품.price}원</p>

					<Info stock={props.stock[id]}></Info>
          <button onClick={() => {
						const newStock = [...props.stock];
						newStock[id] -= 1

						props.setStock(newStock)
					}} className="btn btn-danger">주문하기</button> 
					<button className="btn btn-danger" onClick={() => {
						history.push('/');
					}}>뒤로가기</button> 
        </div>
      </div>
    </div> 
  )
}


function Info(props) {
	return (
		<p>재고: {props.stock}</p>
	)
};
export default Detail